    list-style: none;
    align-items: center;
    /* margin-top: -0.2em; */
}
.navigation-menu-item {
    padding: 0 10px;
}
.navigation-menu-item a:hover {
    /* color: #00b0f0; */
    text-decoration: none;
}
.navigation-menu-icon {
    background: transparent;
    border: none;
    position: relative;
}
.navigation-menu-icon:hover {
    cursor: pointer;
}
.navigation-menu-icon[data-badge]:after {
    content: attr(data-badge);
    position: absolute;
    top: -5px;
    left: 18px;
    font-size: 0.7em;
    background: #27a9f8;
    color: white;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 16px;
    border-radius: 50%;
    box-shadow: 0 0 1px #333;
}
.navigation-toggle {
    display: none;
    width: 30px;
    height: 30px;
    position: relative;
    margin-right: 1em;
    align-self: center;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: 0.5s ease-in-out;
    cursor: pointer;
}
.navigation-toggle span {
    display: block;
    position: absolute;
    height: 5px;
    width: 50%;
    background: #00b0f0;
    opacity: 1;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    transition: 0.25s ease-in-out;
}
.navigation-toggle span:nth-child(even) {
    left: 50%;
    border-radius: 0 9px 9px 0;
}
.navigation-toggle span:nth-child(odd) {
    left: 0px;
    border-radius: 9px 0 0 9px;
}
.navigation-toggle span:nth-child(1),
.navigation-toggle span:nth-child(2) {
    top: 0px;
}
.navigation-toggle span:nth-child(3),
.navigation-toggle span:nth-child(4) {
    top: 12px;
}
.navigation-toggle span:nth-child(5),
.navigation-toggle span:nth-child(6) {
    top: 26px;
}
.navigation-toggle.open span:nth-child(1),
.navigation-toggle.open span:nth-child(6) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.navigation-toggle.open span:nth-child(2),
.navigation-toggle.open span:nth-child(5) {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
.navigation-toggle.open span:nth-child(1) {
    left: 3px;
    top: 5px;
}
.navigation-toggle.open span:nth-child(2) {
    left: calc(50% - 3px);
    top: 5px;
}
.navigation-toggle.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
}
.navigation-toggle.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
}
.navigation-toggle.open span:nth-child(5) {
    left: 3px;
    top: 20px;
}
.navigation-toggle.open span:nth-child(6) {
    left: calc(50% - 3px);
    top: 20px;
}
.navigation-menu-mobile {
    width: 80vw;
